@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInRight {
  0% {
     opacity: 0;
     transform: translateX(100%);
  }
  100% {
     opacity: 1;
     transform: translateX(0);
  }
}